<template>
  <block title="">
      <div class="chart-box" ref="chartRef">

      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const options = {
  tooltip: {
    trigger: 'axis',
    textStyle: {
      align: 'left'
    }
  },
  grid: {
    top: '100', // 
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  calculable: false,
  legend: {
    data: ['', '', ''],
    itemGap: 60,
    textStyle: {
      color: '#e9ebee',
      fontSize: 20
    },
    right: '5%',
    padding: [30, 10]
  },
  xAxis: [
    {
      type: 'category',
      data: [ '04', '08', '12', '16', '20', '24'],
      splitLine: {
        show: false
      },
      axisLabel: {
        show: true,
        textStyle: {
          color: '#fff',
          align: 'center'
        }
      }
    }
  ],
  yAxis: [
    {
      type: 'value',

      axisLabel: {
        formatter: '{value} ',
        textStyle: {
          color: '#fff',
          align: 'right'
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: 'dashed',
          color: '#00e4ff',
          opacity: 0.2
        }
      }
    }
  ],
  series: [
    {
      name: '',
      type: 'custom',
      data: [],
      itemStyle: {
        normal: {
          color: '#18e4c5'
        }
      }
    },
    {
      name: '',
      type: 'custom',
      data: [],
      itemStyle: {
        normal: {
          color: '#3e93f5'
        }
      }
    },
    {
      name: '',
      type: 'line',
      data: [],
      itemStyle: {
        normal: {
          color: '#ffb017'
        }
      }
    }
  ]
}

export default {
  name: 'TemperatureHumidity',
  props: {
    degree: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 
      this.chart.setOption(options)
    },

    _isCenter (values, idx) {
      return !values || values[idx] === undefined || values[idx] === 0
    },

    setChartData () {
      const self = this
      const { humidityMap, temperatureMap, certificateMap } = this.degree
      let newSeries = []
      if (humidityMap) {
        newSeries.push({
          name: '',
          type: 'custom',
          data: humidityMap,
          itemStyle: {
            normal: {
              color: '#18e4c5'
            }
          },
          renderItem: function (params, api) {
            let coord = api.coord([api.value(0), api.value(1)])
            let size = api.size([1, api.value(1)])
            let width = size[0] * 0.35
            let offset = 0
            if (self._isCenter(temperatureMap, api.value(0))) {
              offset = -width / 2
            } else {
              offset = -size[0] * 0.10 / 2 - width
            }
            return {
              type: 'rect',
              shape: {
                x: coord[0] + offset,
                y: coord[1],
                width: width,
                height: size[1]
              },
              style: api.style()
            }
          }
        })
      }
      if (temperatureMap) {
        newSeries.push({
          name: '',
          type: 'custom',
          data: temperatureMap,
          itemStyle: {
            normal: {
              color: '#3e93f5'
            }
          },
          renderItem: function (params, api) {
            let coord = api.coord([api.value(0), api.value(1)])
            let size = api.size([1, api.value(1)])
            let width = size[0] * 0.35
            let offset = 0
            if (self._isCenter(humidityMap, api.value(0))) {
              offset = -width / 2
            } else {
              offset = size[0] * 0.10 / 2
            }
            return {
              type: 'rect',
              shape: {
                x: coord[0] + offset,
                y: coord[1],
                width: width,
                height: size[1]
              },
              style: api.style()
            }
          }
        })
      }
      if (certificateMap) {
        newSeries.push({
          name: '',
          type: 'line',
          data: certificateMap,
          itemStyle: {
            normal: {
              color: '#ffb017'
            }
          }
        })
      }
      options.series = newSeries
      this.chart.setOption(options)
    }
  },
  watch: {
    degree: {
      handler (newValue, oldValue) {
        console.log('data changed....')
        this.setChartData()
      },
      deep: true
    }
  }

}
</script>

<style lang="less" scoped>
.chart-box{
  width: 100%;
  height:100%
}
</style>
